import {Injectable} from '@angular/core';

import {AdaptTranslateService} from '../../common/i18n/index';

import {RxDatetimeTexts} from './adapt-rx-datetime.types';

@Injectable({
  providedIn: 'root'
})
export class AdaptRxDatetimeConfigService {
  constructor(private _ts: AdaptTranslateService) {
  }

  get texts(): RxDatetimeTexts {
    const translation = this._ts.getCurrentLanguage();

    return {
      clock: {
        hhBtn: translation['adapt.datetime.hh'],
        mmBtn: translation['adapt.datetime.mm'],
        ssBtn: translation['adapt.datetime.ss'],
        amBtn: translation['adapt.datetime.am'],
        pmBtn: translation['adapt.datetime.pm'],
        hoursLabel: translation['adapt.datetime.hours'],
        minutesLabel: translation['adapt.datetime.minutes'],
        secondsLabel: translation['adapt.datetime.seconds'],
        meridiemLabel: translation['adapt.datetime.meridiem']
      },
      emptyDate: translation['adapt.datetime.emptyDate'],
      emptyTime: translation['adapt.datetime.emptyTime'],
      emptyYear: translation['adapt.datetime.emptyYear'],
      emptyMonth: translation['adapt.datetime.emptyMonth'],
      cancelBtn: translation['adapt.common.cancel'],
      clearBtn: translation['adapt.common.clearAll'],
      nextBtn: translation['adapt.common.next'],
      applyBtn: translation['adapt.common.ok'],
      calendarBtn: translation['adapt.datetime.calendar'],
      clockBtn: translation['adapt.datetime.clock'],
      startRangeLabel: translation['adapt.datetimeRange.startDate'],
      endRangeLabel: translation['adapt.datetimeRange.endDate'],
      emptyStartRange: translation['adapt.datetimeRange.emptyStart'],
      emptyEndRange: translation['adapt.datetimeRange.emptyEnd']
    };
  }
}
